section.projects {
  width: 100%;
  .nav {
    background-color: rgba(245, 50, 36, .6);
    width: 100%;
    padding: 2.5em 4em;
    margin-right: 1em;
    a {
      text-transform: capitalize;
      font-size: 1.15em;
      text-decoration: none;
      font-weight: 300;
      color: white;
      margin-right: 2em;

      &:hover {
        color: #E44B23; } } }
  a.radius {
    border: 1px solid #E44B23;
    border-radius: 25px;
    padding: .4em 1.1em;
    background-color: blue; }
  ul.grid-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    margin: 0 auto;
    list-style-type: none;
    padding: 10px 50px;
    // try to fix the height of .gif
    li.grid-item {
      width: 300px;
      height: 200px;
      padding: 0px;
      margin: 5px;
      position: relative;
      // BEGIN LOADING STYLE
      transition: all .2s ease-out;
      // hide
      transform: scale(0);
      opacity: 0;
      img {
        width: 100%;
        max-width: 100%;
        border-radius: 4px;
        box-shadow: 0 1px 1px rgba(0,0,0,.5);
        // try to fix height of the .gif
        max-height: 100%; }
      &.show {
        // show them
        opacity: 1;
        transform: scale(1); }
      .info {
        color: white;
        border: 5px solid white;
        width: inherit;
        text-align: center;
        height: inherit;
        padding: 2em 10%;
        background-color: rgba(0, 0, 0, .5);
        // PUT on TOP of the pic
        position: absolute;
        top: auto;
        bottom: 0;
        //hide
        opacity: 0; } }
    li:hover {
      .info {
        opacity: 1; } } } }
