/*! Pushy - v1.0.0 - 2016-3-1
* Pushy is a responsive off-canvas navigation menu using CSS transforms & transitions.
* https://github.com/christophery/pushy/
* by Christopher Yee */
/* Menu Appearance */
.pushy {
  position: fixed;
  width: 200px;
  height: 100%;
  top: 0;
  z-index: 9999;
  background: #191918;
  overflow: auto;
  visibility: hidden;
  -webkit-overflow-scrolling: touch;
  /* enables momentum scrolling in iOS overflow elements */
}

.pushy a {
  display: block;
  color: #b3b3b1;
  padding: 15px 30px;
  text-decoration: none;
  outline: 0;
}

.pushy a:hover {
  color: #FFF;
}

.pushy ul:first-child {
  margin-top: 10px;
}

.pushy.pushy-left {
  left: 0;
}

.pushy.pushy-right {
  right: 0;
}

/* Menu Movement */
.pushy-left {
  -webkit-transform: translate3d(-200px, 0, 0);
  -ms-transform: translate3d(-200px, 0, 0);
  transform: translate3d(-200px, 0, 0);
}

.pushy-open-left #container,
.pushy-open-left .push {
  -webkit-transform: translate3d(200px, 0, 0);
  -ms-transform: translate3d(200px, 0, 0);
  transform: translate3d(200px, 0, 0);
}

.pushy-right {
  -webkit-transform: translate3d(200px, 0, 0);
  -ms-transform: translate3d(200px, 0, 0);
  transform: translate3d(200px, 0, 0);
}

.pushy-open-right #container,
.pushy-open-right .push {
  -webkit-transform: translate3d(-200px, 0, 0);
  -ms-transform: translate3d(-200px, 0, 0);
  transform: translate3d(-200px, 0, 0);
}

.pushy-open-left .pushy,
.pushy-open-right .pushy {
  -webkit-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

/* Menu Transitions */
#container,
.pushy,
.push {
  transition: transform 0.2s cubic-bezier(0.16, 0.68, 0.43, 0.99);
}

/* Site Overlay */
.site-overlay {
  display: none;
}

.pushy-open-left .site-overlay,
.pushy-open-right .site-overlay {
  display: block;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9998;
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-animation: fade 500ms;
  animation: fade 500ms;
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Submenu Appearance */
.pushy-submenu {
  /* Submenu Icon */
}

.pushy-submenu ul {
  padding-left: 15px;
  transition: max-height 0.2s ease-in-out;
}

.pushy-submenu ul .pushy-link {
  transition: opacity 0.2s ease-in-out;
}

.pushy-submenu > a {
  position: relative;
}

.pushy-submenu > a::after {
  content: '';
  display: block;
  height: 11px;
  width: 8px;
  position: absolute;
  top: 50%;
  right: 15px;
  background: url("../img/arrow.svg") no-repeat;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  transition: transform 0.2s;
}

/* Submenu Movement */
.pushy-submenu-closed ul {
  max-height: 0;
  overflow: hidden;
}

.pushy-submenu-closed .pushy-link {
  opacity: 0;
}

.pushy-submenu-open {
  /* Submenu Icon */
}

.pushy-submenu-open ul {
  max-height: 1000px;
}

.pushy-submenu-open .pushy-link {
  opacity: 1;
}

.pushy-submenu-open a::after {
  -webkit-transform: translateY(-50%) rotate(90deg);
  -ms-transform: translateY(-50%) rotate(90deg);
  transform: translateY(-50%) rotate(90deg);
}

.no-csstransforms3d .pushy-submenu-closed ul {
  max-height: none;
  display: none;
}

section.sidebar nav.pushy.pushy-left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

section.sidebar nav.pushy.pushy-left .logo {
  padding: 0px;
  width: 100px;
  height: 100px;
  background-image: url(../images/logo-m.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  margin: 40px auto;
}

section.sidebar nav.pushy.pushy-left h1#intro {
  color: white;
  text-align: center;
  padding: 1em;
  font-size: 1.4em;
  line-height: 1.2em;
}

section.sidebar nav.pushy.pushy-left h1#intro b {
  color: #e44b23;
}

section.sidebar nav.pushy.pushy-left ul {
  margin: 0;
  padding: 0;
}

section.sidebar nav.pushy.pushy-left ul li {
  list-style-type: none;
}

section.sidebar nav.pushy.pushy-left ul li a:link, section.sidebar nav.pushy.pushy-left ul li a:visited {
  background-color: #e54f28;
  color: #F7F7F2;
}

section.sidebar nav.pushy.pushy-left ul li a:hover, section.sidebar nav.pushy.pushy-left ul li a:active {
  background-color: #c63c18;
}

.footer, .social-media {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 2vh;
}

.footer p#follow-me, .footer small, .social-media p#follow-me, .social-media small {
  text-align: center;
  color: #C8D5DE;
  margin: 0;
  font-weight: 100;
}

.footer .social-media, .social-media .social-media {
  flex-flow: row wrap;
  margin: 0;
}

.footer .social-media a, .social-media .social-media a {
  padding: 10px;
  margin: 10px;
}

.footer .social-media a img, .social-media .social-media a img {
  width: 23px;
  height: 23px;
}

address a {
  font-size: .8em;
}

section.projects {
  width: 100%;
}

section.projects .nav {
  background-color: rgba(245, 50, 36, 0.6);
  width: 100%;
  padding: 2.5em 4em;
  margin-right: 1em;
}

section.projects .nav a {
  text-transform: capitalize;
  font-size: 1.15em;
  text-decoration: none;
  font-weight: 300;
  color: white;
  margin-right: 2em;
}

section.projects .nav a:hover {
  color: #E44B23;
}

section.projects a.radius {
  border: 1px solid #E44B23;
  border-radius: 25px;
  padding: .4em 1.1em;
  background-color: blue;
}

section.projects ul.grid-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin: 0 auto;
  list-style-type: none;
  padding: 10px 50px;
}

section.projects ul.grid-container li.grid-item {
  width: 300px;
  height: 200px;
  padding: 0px;
  margin: 5px;
  position: relative;
  transition: all .2s ease-out;
  transform: scale(0);
  opacity: 0;
}

section.projects ul.grid-container li.grid-item img {
  width: 100%;
  max-width: 100%;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  max-height: 100%;
}

section.projects ul.grid-container li.grid-item.show {
  opacity: 1;
  transform: scale(1);
}

section.projects ul.grid-container li.grid-item .info {
  color: white;
  border: 5px solid white;
  width: inherit;
  text-align: center;
  height: inherit;
  padding: 2em 10%;
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  top: auto;
  bottom: 0;
  opacity: 0;
}

section.projects ul.grid-container li:hover .info {
  opacity: 1;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Dosis', sans-serif;
  height: 100%;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

.container {
  display: flex;
  height: 100%;
  width: 100%;
}
