// sidebar styles
@import "pushy";
section.sidebar {
  nav.pushy.pushy-left {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .logo {
      padding: 0px;
      width: 100px;
      height: 100px;
      background-image: url(../images/logo-m.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      margin: 40px auto; }
    h1#intro {
      color: white;
      text-align: center;
      padding: 1em;
      font-size: 1.4em;
      line-height: 1.2em;
      b {
        color: #e44b23; } }
    ul {
      margin: 0;
      padding: 0;
      li {
        list-style-type: none;
        a:link, a:visited {
          background-color: lighten(#e44b23, 1%);
          color: #F7F7F2; }
        a:hover, a:active {
          background-color: darken(#e44b23, 8%); } } } } }
//cool light blue color: rgb(200, 213, 222), #C8D5DE
.footer, .social-media {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 2vh;
  p#follow-me, small {
    text-align: center;
    color: #C8D5DE;
    margin: 0;
    font-weight: 100; }
  .social-media {
    flex-flow: row wrap;
    margin: 0;
    a {
      padding: 10px;
      margin: 10px;
      img {
        width: 23px;
        height: 23px; } } } }
address a {
  font-size: .8em; }
